
<style scoped>
.step-box {
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
}

.next-btn {
  width: 100%;
  margin-top: 80px;
  margin-bottom: 20px;
}

.input-box {
  margin-top: 5px;
  margin-bottom: 5px;
}

.root {
  padding: 20px;
}
</style>


<template>
  <div class="root">
    <h1>忘记密码</h1>

    <el-steps :active="display.current" finish-status="success">
      <el-step title="填写 Email 地址"></el-step>
      <el-step title="验证 Email "></el-step>
      <el-step title="设置新密码"></el-step>
      <el-step title="完成"></el-step>
    </el-steps>

    <div class="step-box" v-show="display.current==display.email">
      <h2>填写 Email 地址</h2>
      <el-input class="input-box" v-model="email" placeholder="Email 地址"></el-input>
      <el-button type="primary" class="next-btn" @click="nextForInputEmail">下一步</el-button>
    </div>

    <div class="step-box" v-show="display.current==display.verify">
      <h2>验证 Email</h2>
      <el-input class="input-box" v-model="vericode" placeholder="验证码">
        <el-button
          slot="append"
          icon="el-icon-message"
          @click="onClickRequestSendMail"
        >{{ verifyCodeButtonText }}</el-button>
      </el-input>
      <el-button type="primary" class="next-btn" @click="nextForVerifyEmail">下一步</el-button>
    </div>

    <div class="step-box" v-show="display.current==display.password">
      <h2>设置新密码</h2>
      <el-input class="input-box" v-model="password" placeholder="输入：新密码" type="password"></el-input>
      <el-input class="input-box" v-model="password2" placeholder="重复：新密码" type="password"></el-input>
      <el-button type="primary" class="next-btn" @click="nextForSetupPassword">提交</el-button>
    </div>

    <div class="step-box" v-show="display.current==display.success">
      <h2>成功</h2>
      <p>恭喜！你已经成功设置新密码。</p>
      <el-button type="primary" class="next-btn" @click="nextForOK">立即登录</el-button>
    </div>
  </div>
</template>


<script>
var sha256 = require("js-sha256").sha256;

export default {
  name: "SignUpView",

  data() {
    return {
      email: "",
      vericode: "",
      password: "",
      password2: "",
      active: 0,
      verifyCodeButtonText: "获取验证码",
      verifyCodeTimeout: 0,

      form: {
        email: "",
        token: "",
        code: "",
        password: ""
      },

      display: {
        current: 0,
        email: 0,
        verify: 1,
        password: 2,
        success: 3
      }
    };
  },

  methods: {
    nextForInputEmail() {
      this.form.email = this.email;
      this.display.current++;
    },

    nextForVerifyEmail() {
      this.form.code = this.vericode;

      var self = this;
      var on_ok = res => {
        self.display.current++;
      };
      var on_err = res => {
        self.showError(res);
      };

      this.requestVerifyVcode(on_ok, on_err);
    },

    nextForSetupPassword() {
      var password = this.verifyAndHashPassword(this.password, this.password2);
      if (password == null) {
        return;
      }
      this.form.password = password;
      var self = this;
      var on_ok = resp => {
        self.display.current++;
      };
      var on_error = resp => {
        self.showError(resp);
      };
      this.requestResetPassword(on_ok, on_error);
    },

    nextForOK() {
      this.$router.push("/login");
    },

    onClickRequestSendMail() {
      if (this.verifyCodeTimeout > 0) {
        return;
      }

      var self = this;
      this.verifyCodeTimeout = 60;
      this.verifyCodeButtonText = "";

      var timer = () => {
        var timeout = self.verifyCodeTimeout;
        if (timeout > 0) {
          self.verifyCodeButtonText = timeout + "秒后重试";
          setTimeout(timer, 1000);
        } else {
          self.verifyCodeButtonText = "获取验证码";
        }
        self.verifyCodeTimeout = timeout - 1;
      };

      this.requestSendMail();
      timer();
    },

    verifyAndHashPassword(pass1, pass2) {
      if (pass1 != pass2) {
        this.$message("密码不相等。");
        return null;
      } else if (pass1.length < 4) {
        this.$message("请输入有效的密码。");
        return null;
      } else {
        var pass = pass1;
        pass = sha256(pass);
        pass = sha256(pass);
        return pass;
      }
    },

    requestResetPassword(on_ok, on_error) {
      var code = this.form.code;
      var email = this.form.email;
      var secret = this.form.password;
      var token = this.form.token;

      // rest
      var type = "ResetPassword";
      var id = null;
      var data = {
        auth: { name: email, secret },
        verifyEmail: { email, token, code }
      };

      this.$store.dispatch("rest/post", { type, id, data, on_ok, on_error });
    },

    requestVerifyVcode(on_ok, on_error) {
      var code = this.form.code;
      var email = this.form.email;
      var token = this.form.token;

      // rest
      var type = "VerifyEmail";
      var id = token;
      var data = {
        verifyEmail: { email, token, code }
      };

      this.$store.dispatch("rest/put", { type, id, data, on_ok, on_error });
    },

    requestSendMail(on_ok, on_error) {
      var self = this;

      var code = "";
      var token = "";
      var email = this.form.email;

      // rest
      var type = "VerifyEmail";
      var id = null;
      var data = {
        verifyEmail: { email, token, code }
      };

      on_ok = this.handleEvent(on_ok, res => {
        var token = res.verifyEmail.token;
        self.form.token = token;
      });

      this.$store.dispatch("rest/post", { type, id, data, on_ok, on_error });
    },

    handleEvent(l1, l2) {
      var handler = res => {
        if (l2 != null) {
          l2(res);
        }
        if (l1 != null) {
          l1(res);
        }
      };
      return handler;
    },

    showError(err) {
      this.$message("Error");
    }
  }
};
</script>
